  margin-top: 40px;
}

.pages {
  grid-area: pages;
}
.pages ul {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.pages a {
  font-size: 16px;
  line-height: 1.5;
  color: rgba(74, 85, 108, 0.4392156863);
  text-decoration: none;
}

.contacts {
  grid-area: contacts;
}
.contacts ul {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.contacts a {
  font-size: 16px;
  line-height: 1.5;
  color: rgba(74, 85, 108, 0.4392156863);
  text-decoration: none;
}

.footer-contact {
  padding-top: 66px;
  padding-bottom: 66px;
  padding-left: 15px;
  padding-right: 15px;
  border-bottom: 1px #e4e4e6 solid;
  display: grid;
  grid-template: auto auto/auto auto;
  column-gap: 32px;
  row-gap: 32px;
  justify-content: center;
  grid-template-areas: "logo logo" "pages contacts";
}
@media screen and (min-width: 1280px) {
  .footer-contact {
    display: grid;
    grid-template: auto/320px 400px 400px;
    column-gap: 32px;
    row-gap: 0;
    grid-template-areas: "logo pages contacts";
  }
}

.footer-list-tytle {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 22px;
}

.address-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 32px;
  padding-bottom: 40px;
  gap: 16px;
}
@media screen and (min-width: 1280px) {
  .address-footer {
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
  }
}

.byMade {
  font-size: 12px;
  line-height: 1.2;
  color: #041434;
  font-weight: 700;
}
.byMade span {
  display: inline-block;
  vertical-align: middle;
  width: 12px;
  height: 12px;
}
.byMade span:before {
  width: 100%;
  height: 100%;
  background-position: -131px -58px;
}
.byMade a {
  text-decoration: none;
  color: #041434;
  font-weight: 800;
}

.linkSocial {
  display: flex;
  align-items: center;
  gap: 24px;
}
.linkSocial a {
  display: block;
  width: 25px;
  height: 25px;
}
.linkSocial a:before {
  width: 100%;
  height: 100%;
  background-position: -98px -95px;
}
.linkSocial a + a:before {
  background-position: -179px -54px;
}
.linkSocial a + a + a:before {
  background-position: -179px -9px;
}

